@import url("https://fonts.googleapis.com/css?family=Roboto:300,300i,400,400i,500");
@import url("https://fonts.googleapis.com/css?family=Yanone+Kaffeesatz:300,400,700");
@import url("https://fonts.googleapis.com/css?family=Slabo+27px");
.main-page .main-abilities {
  background-image: url("/img/html-ability.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  transition: padding .25s;
}

.main-page .main-abilities .background-suppressor {
  background-color: rgba(0, 0, 0, 0.4);
  padding: 90px 0px 90px 0px;
}

.main-page .main-abilities .header {
  font-family: 'Slabo 27px', serif;
  letter-spacing: 2px;
  color: #fff;
  text-align: center;
  font-weight: 300;
  font-size: 40px;
  margin: 0px 0px 15px 0px;
  transition: font-size .25s;
  text-shadow: 0px 0px 25px black;
}

.main-page .main-abilities .title-bar {
  height: 1px;
  border-bottom: 1px solid #e5e3df;
  width: 50%;
  margin: 0 auto;
  margin-bottom: 40px;
  position: relative;
}

.main-page .main-abilities .title-bar .bar-accent {
  position: absolute;
  border: 1px solid #ecc969;
  height: 9px;
  width: 9px;
  border-radius: 50%;
  top: -4px;
  left: 47%;
}

.main-page .main-abilities .main-container .legend {
  padding-right: 50px;
}

.main-page .main-abilities .main-container .legend .header {
  color: #fff;
  font-size: 23px;
  text-align: right;
}

.main-page .main-abilities .main-container .legend .ability-level {
  color: #fff;
  text-align: right;
}

.main-page .main-abilities .main-container .legend .ability-level .fa-circle {
  margin-left: 10px;
}

.main-page .main-abilities .main-container .legend .ability-level .proficient {
  color: #51bf87;
}

.main-page .main-abilities .main-container .legend .ability-level .competent {
  color: #ecc969;
}

.main-page .main-abilities .main-container .legend .ability-level .novice {
  color: #8a4b70;
}

.main-page .main-abilities .main-container .abilities {
  padding: 40px 150px 0px 150px;
}

.main-page .main-abilities .main-container .abilities .abilities-list {
  position: relative;
  margin: 0px;
  padding: 0px;
  list-style: none;
  text-align: center;
}

.main-page .main-abilities .main-container .abilities .abilities-list .item-container {
  position: relative;
  display: inline-block;
}

.main-page .main-abilities .main-container .abilities .abilities-list .item-container .practice {
  position: absolute;
  font-size: 30px;
}

.main-page .main-abilities .main-container .abilities .abilities-list .item {
  position: relative;
  cursor: pointer;
  display: inline-block;
  padding: 7px 15px;
  margin: 0px 5px 10px 5px;
  color: #fff;
  font-size: 19px;
  font-weight: 500;
  transition: background-color .25s;
  -webkit-box-shadow: 0px 0px 40px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 0px 40px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 40px 0px rgba(0, 0, 0, 0.75);
}

.main-page .main-abilities .main-container .abilities .abilities-list .proficient {
  background-color: #51bf87;
}

.main-page .main-abilities .main-container .abilities .abilities-list .proficient:hover {
  background-color: #1a7a4a;
}

.main-page .main-abilities .main-container .abilities .abilities-list .competent {
  background-color: #ecc969;
}

.main-page .main-abilities .main-container .abilities .abilities-list .competent:hover {
  background-color: #ac8822;
}

.main-page .main-abilities .main-container .abilities .abilities-list .novice {
  background-color: #8a4b70;
}

.main-page .main-abilities .main-container .abilities .abilities-list .novice:hover {
  background-color: #4a2139;
}

/* Large Devices, Wide Screens */
@media only screen and (max-width: 1200px) and (min-width: 993px) {
  .main-page .main-abilities .background-suppressor {
    padding: 85px 0px 35px 0px;
  }
  .main-page .main-abilities .header {
    font-size: 38px;
  }
  .main-page .main-abilities .main-container .abilities {
    padding: 40px 120px 0px 120px;
  }
  .main-page .main-abilities .main-container .abilities .abilities-list {
    margin: 0px;
    padding: 0px;
  }
  .main-page .main-abilities .main-container .abilities .abilities-list .item {
    padding: 7px 15px;
    margin: 0px 5px 10px 5px;
    font-size: 19px;
  }
}

